	data() {
		return {
			imgUrl: this.$imgUrl,
			dataFormList: [],
			// {
			// 	imgUrl: "../static/image/KnowUsCelebritySaysA.png",
			// 	title: "一天一蘋果 醫生遠離我",
			// 	content: "Steve Jobs"
			// },{
